.buyers-main {
  height: 290px;
  background-image: url("../img/buyers-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.buyers {
  padding: 0 0 470px;
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 28px;
  }
  &__item {
    background: #1F1F31;
    border-radius: 15px;
    padding: 30px 20px;
  }
  &__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    &-img {
      position: relative;
      width: 150px;
      img {
        width: 100%;
      }
    }
    &-edit {
      position: absolute;
      right: 0;
      bottom: 0;
      max-width: 40px;
    }
    &-name {
      font-family: 'Neue Einstellung';
      font-weight: 700;
      font-size: 24px;
      line-height: 120%;
      margin-top: 15px;
      text-align: center;
    }
  }
  &__personality {
    &-title {
      font-weight: 500;
      font-size: 20px;
      line-height: 120%;
    }
    &-info {
      display: grid;
      grid-template-columns: 0.5fr 1fr;
      gap: 25px;
      margin-top: 35px;
      padding: 10px 20px;
      background: #323248;
      border-radius: 10px;
    }
    &-data {
      font-size: 13px;
      line-height: 120%;
      &:nth-child(odd) {
        color: #7D7D97;
      }
    }
  }
  &__interests {
    &-title {
      font-weight: 500;
      font-size: 20px;
      line-height: 120%;
    }
    &-games {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 15px;
      margin-top: 30px;
    }
    &-game {
      img {
        width: 100%;
      }
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    &-title {
      font-weight: 500;
      font-size: 16px;
      line-height: 120%;
    }
    &-purchased {
      display: flex;
      align-items: center;
      margin: 25px 0;
      span {
        font-family: 'Neue Einstellung';
        font-weight: 700;
        font-size: 32px;
        line-height: 120%;
        margin-left: 10px;
      }
    }
    &-discount {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 25px;
    }
  }
  &__discount-number {
    font-family: 'Neue Einstellung';
    font-weight: 700;
    font-size: 48px;
    line-height: 105%;
  }
  &__discount-desc {
    font-size: 12px;
    line-height: 120%;
    color: #7D7D97;
    margin-left: 15px;
    a {
      color: #AE7CFF;
    }
  }
}

@media screen and (max-width: 1400px) {
  .buyers {
    &__items {
      grid-template-columns: 1fr 1fr;
    }
    &__info {
      justify-content: start;
    }
  }
}

@media screen and (max-width: 768px) {
  .buyers-main {
    display: none;
  }
  .buyers {
    padding: 80px 0;
    &__items {
      grid-template-columns: 1fr;
    }
    &__item2 {
      order: -2;
    }
    &__item3 {
      order: -3;
    }
    &__logo-name {
      font-size: 20px;
    }
    &__info {
      &-purchased span {
        font-size: 30px;
      }
    }
    &__discount-number {
      font-size: 30px;
    }
  }
}

@media screen and (max-width: 576px) {
.buyers {
  &__logo {
    &-img {
      width: 120px;
    }
    &-name {
      font-size: 18px;
      margin-top: 10px;
    }
  }
}
}