.orders {
  padding: 90px 0 500px;
  &__inner {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  &__left {
    width: 387px;
    margin-right: 28px;
    &-wrapp {
      background: #1F1F31;
      border-radius: 15px;
      padding: 20px 20px 45px 20px;
    }
    &-mob {
      display: none;
    }
  }
  .select {
    &__wrap {
      position: relative;
      z-index: 1;
      &--active {
        .select__list {
          display: block;
        }
      }
    }
    &__list-default {
      width: 100%;
      height: 50px;
      background: #323248;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
      border-radius: 7px;
      display: flex;
      align-items: center;
      padding: 0 35px 0 20px;
      position: relative;
      cursor: pointer;
      li {
        width: 100%;
        font-size: 20px;
        line-height: 120%;
      }
      .orders__tab--active {
        background: transparent;
      }
    }
    &__list {
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      background: #323248;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
      border-radius: 7px;
      display: none;
      li {
        height: 50px;
        display: flex;
        align-items: center;
        padding: 0 20px;
        cursor: pointer;
        &:hover {
          background: #49495F;
        }
        &:first-child:hover {
          border-radius: 7px 7px 0 0;
        }
        &:last-child:hover {
          border-radius: 0 0 7px 7px;
        }
      }
      .orders__tab--active {
        background: transparent;
      }
      .li--active {
        background: #49495F;
        &:first-child {
          border-radius: 7px 7px 0 0;
        }
        &:last-child {
          border-radius: 0 0 7px 7px;
        }
      }
    }
    &-arrow {
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translate(0, -50%);
    }
  }
  &__right {
    width: calc(100% - 415px);
    display: none;
    &--active {
      display: block;
    }
  }
  &__tab {
    height: 60px;
    border-radius: 7px;
    padding: 0 15px;
    font-size: 20px;
    line-height: 120%;
    display: flex;
    align-items: center;
    cursor: pointer;
    &--active {
      background: #101016;
    }
    &-mob {
      background: transparent;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }
  &__item {
    min-height: 100px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    &-left {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: calc(100% - 415px);
      background: #323248;
      border-radius: 15px;
      padding: 0 20px;
    }
    &-right {
      display: flex;
      justify-content: space-between;
      align-items: center;
      max-width: 405px;
      width: 100%;
      background: #323248;
      border-radius: 15px;
      padding: 0 20px;
      margin-left: 10px;
    }
    &-desc {
      padding-right: 10px;
    }
    &-data {
      display: flex;
      align-items: center;
      flex-shrink: 0;
    }
    &-title {
      font-weight: 500;
      font-size: 16px;
      line-height: 120%;
    }
    &-info {
      display: flex;
      align-items: center;
      margin-top: 10px;
    }
    &-price {
      display: flex;
      align-items: center;
    }
    &-discount {
      font-size: 14px;
      line-height: 120%;
      padding: 4px 8px;
      background: #9039FF;
      border-radius: 5px;
      margin-left: 10px;

    }
    &-quantity {
      text-align: center;
      padding: 0 20px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: -10px;
        left: 0;
        width: 1px;
        height: 60px;
        background: #45456D;
      }
      &:before {
        content: '';
        position: absolute;
        top: -10px;
        right: 0;
        width: 1px;
        height: 60px;
        background: #45456D;
      }
    }
    &-bonus {
      text-align: center;
      padding: 0 30px;
      position: relative;
      &:before {
        content: '';
        position: absolute;
        top: -10px;
        right: 0;
        width: 1px;
        height: 60px;
        background: #45456D;
      }
    }
    &-btns {
      display: flex;
      align-items: center;
      padding-left: 30px;
      padding-right: 10px;
    }
    &-btn {
      width: 50px;
      height: 50px;
      background: #535370;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0;
      cursor: unset;
      &:not(:first-child) {
        margin-left: 10px;
      }
    }
    .basket {
      &:hover {
        background: #DE6363;
        svg path {
          fill: #ffffff;
        }
      }
    }
    .like {
      &:hover {
        background: #61B971;
        svg path {
          fill: #ffffff;
        }
      }
    }
    .expectation {
      width: auto;
      height: auto;
      background: transparent;
    }
    .game {
      width: auto;
      height: auto;
      background: transparent;
    }
    .paid2 {
      width: auto;
      height: auto;
      background: transparent;
      margin-left: 25px;
    }
    .deleted {
      width: auto;
      height: auto;
      background: transparent;
    }
    .game2 {
      width: auto;
      height: auto;
      background: transparent;
    }
    &-user {
      display: flex;
      align-items: center;
    }
  }
  .dismissed {
    background: #000000;
    .orders__item-left,
    .orders__item-right {
      opacity: 0.6;
      &:hover {
        opacity: 1;
      }
    }
  }
  &__price-text {
    font-size: 18px;
    line-height: 120%;
    margin-left: 4px;
    span {
      font-size: 14px;
      line-height: 120%;
      color: #9696AE;
    }
  }
  &__quantity-title {
    font-size: 14px;
    line-height: 120%;
    color: #CBCBEA;
  }
  &__quantity-number {
    font-weight: 500;
    font-size: 18px;
    line-height: 105%;
    margin-top: 8px;
  }
  &__bonus-title {
    font-size: 14px;
    line-height: 120%;
    color: #CBCBEA;
  }
  &__bonus-amount {
    display: flex;
    align-items: center;
    margin-top: 9px;
    img {
      flex-shrink: 0;
    }
    span {
      font-weight: 500;
      font-size: 18px;
      line-height: 120%;
      margin-left: 4px;
    }
  }
  &__user-logo {
    width: 70px;
    img {
      width: 100%;
    }
  }
  &__user-title {
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
  }
  &__user-desc {
    margin-left: 10px;
  }
  &__user-date {
    display: flex;
    align-items: center;
    margin-top: 5px;
    span:nth-child(1) {
      font-size: 14px;
      line-height: 120%;
      color: #CBCBEA;
    }
    span:nth-child(2) {
      font-size: 14px;
      line-height: 120%;
      color: #CBCBEA;
      margin-left: 15px;
    }
  }
  &__user-btn {
    width: 110px;
    height: 50px;
    background: transparent;
    font-size: 14px;
    img {
      display: none;
    }
  }
}

@media screen and (max-width: 1610px) {
  .orders {
    &__item {
      &-quantity {
        padding: 0 10px;
      }
      &-bonus {
        padding: 0 10px;
      }
      &-btns {
        padding-left: 15px;
      }
      &-btn {
        width: 40px;
        height: 40px;
      }
    }
  }
}

@media screen and (max-width: 1550px) {
  .orders {
    &__inner {
      display: block;
    }
    &__left {
      margin-right: 0;
    }
    &__right {
      width: 100%;
      margin-top: 28px;
    }
    &__item {
      &-quantity {
        padding: 0 20px;
      }
      &-bonus {
        padding: 0 20px;
      }
      &-btns {
        padding-left: 20px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .orders {
   &__item {
     display: block;
     min-height: unset;
     &-left {
       width: 100%;
       padding: 15px 20px;
       border-radius: 15px 15px 0 0;
     }
    &-right {
      justify-content: start;
      max-width: 100%;
      margin-left: 0;
      padding: 15px 20px;
      border-radius: 0 0 15px 15px;
    }
     &-quantity {
       &:after,
       &:before {
         display: none;
       }
     }
     &-bonus {
       &:before {
         display: none;
       }
     }
   }
    &__user-btn {
      margin-left: 15px;
    }
  }
}

@media screen and (max-width: 768px) {
 .orders {
   padding: 80px 0;
   &__item {
     &-left {
       display: block;
     }
     &-data {
       margin-top: 30px;
     }
     &-quantity {
       padding: 0;
     }
     &-bonus {
       padding: 0;
       margin-left: 15px;
     }
     &-btns {
       padding: 0;
       margin-left: 15px;
     }
   }
 }
}

@media screen and (max-width: 576px) {
  .orders {
    padding: 40px 0;
    &__left {
      display: none;
      &-mob {
        display: block;
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .orders {
    &__item {
      &-left {
        padding: 15px 10px;
      }
      &-right {
        justify-content: space-between;
        padding: 15px 10px;
      }
      &-data {
        justify-content: space-between;
      }
    }
    &__user-btn {
      width: 40px;
      height: 40px;
      background: #535370;
      border: unset;
      border-radius: 50%;
      span {
        display: none;
      }
      img {
        display: block;
      }
    }
  }
}

@media screen and (max-width: 360px) {
  .orders {
    &__item {
      &-right {
        display: block;
      }
      &-info {
        display: block;
      }
      &-discount {
        display: inline-block;
        margin: 10px 0 0 0;
      }
    }
    &__user-btn {
      margin: 20px 0 0 0;
    }
  }
}