.body-bg {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background: #5C33CF;
  }
}
.authorization {
  position: relative;
  z-index: 1;
  padding: 20px 0 0;
  .container {
    max-width: 1820px;
  }
  &__top {
    display: flex;
    justify-content: space-between;
  }
  &__logo {
    display: block;
    width: 110px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__close {
    width: 40px;
    height: 40px;
    background: #45456D;
    border-radius: 50%;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__inner {
    display: flex;
    margin-top: 105px;
  }
  &__left {
    width: 50%;
    position: relative;
    margin-top: -30px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 750px;
      height: 750px;
      background: radial-gradient(50% 50% at 50% 50%, rgba(254, 184, 255, 0.8) 0%, rgba(139, 165, 255, 0) 100%);
    }
    &-wrapp {
      position: relative;
      z-index: 1;
    }
  }
  &__right {
    width: 50%;
    max-width: 630px;
    margin: 0 auto;
  }
  &__title {
    font-family: 'Earths Mightiest';
    font-weight: 700;
    font-size: 90px;
    line-height: 120%;
  }
  &__img {
    width: 700px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__right-title {
    font-weight: 700;
    font-size: 40px;
    line-height: 120%;
  }
  &__tabs {
    margin-top: 55px;
    &-head {
      display: flex;
      align-items: center;
    }
    &-body {
      margin-top: 65px;
    }
    &-content {
      display: none;
      &--active {
        display: block;
      }
    }
  }
  &__tab-btn {
    font-weight: 500;
    font-size: 24px;
    line-height: 120%;
    color: #FFFFFF;
    cursor: pointer;
    position: relative;
    &--active {
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: -5px;
        transform: translate(-50%, 0);
        width: 30px;
        height: 4px;
        background: #9039FF;
      }
    }
    &:not(:first-child) {
      margin-left: 50px;
    }
  }
  &__form {
    &-info {
      font-size: 16px;
      line-height: 120%;
      color: #AE7CFF;
    }
    &-btn {
      width: 160px;
      height: 50px;
      margin: 11px 0 0 auto;
    }
    &-btn2 {
      width: 200px;
      height: 50px;
      margin: 11px 0 0 auto;
    }
    &-radio {
      display: flex;
      align-items: center;
    }
    &-group {
      &:not(:first-child) {
        margin-top: 28px;
      }
      span {
        margin-bottom: 11px;
      }
      input {
        width: 100%;
        height: 50px;
      }
    }
  }
  .password-input {
    position: relative;
  }
  .password-btn {
    position: absolute;
    z-index: 1;
    top: 50%;
    right: 20px;
    transform: translate(0, -50%);
    width: 22px;
    cursor: pointer;
    &:after {
      content: '';
      position: absolute;
      top:40%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(-45deg);
      width: 30px;
      height: 3px;
      background: #7D7D97;
      transition: all 0.3s;
      opacity: 0;
      pointer-events: none;
    }
    img {
      width: 100%;
    }
  }
  .view {
    &:after {
      opacity: 1;
      pointer-events: all;
    }
  }
}
.recovery {
  width: 50%;
  max-width: 630px;
  margin: 0 auto;
  display: none;
  &__title {
    font-size: 40px;
    line-height: 120%;
    font-weight: 500;
  }
  &__desc {
    font-size: 18px;
    line-height: 120%;
    color: #9696AE;
    margin-top: 12px;
  }
  &__form {
    margin-top: 55px;
    &-group {
      &:not(:first-child) {
        margin-top: 50px;
      }
      span {
        margin-bottom: 11px;
      }
      input {
        width: 100%;
        height: 50px;
      }
    }
    &-btns {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &-back {
      width: 50px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
    }
    &-btn {
      width: 160px;
      height: 50px;
    }
  }
}

@media screen and (max-width: 1600px) {
  .authorization {
    &__left {
      padding: 0 30px;
      &:after {
        width: 500px;
        height: 500px;
      }
    }
    &__right {
      padding: 0 30px;
      max-width: 100%;
    }
    &__title {
      font-size: 72px;
    }
    &__img {
      width: 500px;
    }
  }
  .recovery {
    padding: 0 30px;
    max-width: 100%;
  }
}

@media screen and (max-width: 1200px) {
  .body-bg {
    &:after {
    display: none;
    }
  }
  .authorization {
    padding: 0 0 65px;
    &__top {
      position: absolute;
      z-index: 3;
      top: 0;
      left: 0;
      width: 100%;
      padding: 20px 15px 0;
    }
    &__inner {
      display: block;
      margin-top: 0;
    }
    &__left {
      width: 100%;
      position: relative;
      padding: 80px 0 0 0;
      margin-top: 0;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: -15px;
        width: calc(100% + 30px);
        height: 100%;
        background: #5C33CF;
      }
      &-wrapp {
        max-width: 600px;
        margin: 0 auto;
      }
    }
    &__right {
      max-width: 600px;
      width: 100%;
      padding: 60px 0 0 0;
    }
    &__img {
      margin: 0 auto;
      img {
        margin-bottom: -40px;
      }
    }
  }
  .recovery {
    max-width: 600px;
    width: 100%;
    padding: 60px 0 0 0;
  }
}

@media screen and (max-width: 768px) {
  .authorization {
    &__left {
      &:after {
        width: 300px;
        height: 300px;
      }
      &-wrapp {
        max-width: 100%;
      }
    }
    &__right {
      max-width: 100%;
      &-title {
        font-size: 32px;
      }
    }
    &__title {
      font-size: 48px;
    }
    &__img {
      width: 300px;
    }
    &__tabs {
      margin-top: 40px;
      &-body {
        margin-top: 40px;
      }
    }
    &__tab-btn {
      font-size: 20px;
      &:not(:first-child) {
        margin-left: 35px;
      }
    }
  }
  .recovery {
    max-width: 100%;
    &__title {
      font-size: 32px;
    }
    &__form {
      margin-top: 40px;
      &-group {
        &:not(:first-child) {
          margin-top: 28px;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .authorization {
    &__logo {
      width: 80px;
    }
    &__tab-btn {
      &:not(:first-child) {
        margin-left: 20px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .authorization {
    &__title {
      font-size: 38px;
    }
  }
}