.services2-top {
  padding: 70px 0;
  background-image: url("../img/services-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    display: flex;
    align-items: center;
    &-img {
      width: 150px;
      img {
        width: 100%;
        height: auto;
      }
    }
    &-desc {
      margin-left: 30px;
    }
    &-name {
      font-family: 'Neue Einstellung';
      font-weight: 700;
      font-size: 36px;
      line-height: 120%;
    }
    &-info {
      display: inline-flex;
      align-items: center;
      background: #323248;
      border-radius: 5px;
      padding: 6px 8px;
      margin-top: 10px;
      span {
        font-size: 14px;
        line-height: 120%;
        margin-left: 5px;
      }
    }
  }
  &__socials {
    display: flex;
    align-items: center;
  }
  &__social {
    width: 50px;
    height: 50px;
    background: rgba(22, 22, 40, 0.5);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    &:not(:first-child) {
      margin-left: 10px;
    }
    img {
      max-width: 26px;
      height: auto;
    }
  }
  .discord:hover {
    background: #685bed;
  }
  .youtube:hover {
    background: #ff001a;
  }
  .twitch:hover {
    background: #a02cf1;
  }
}
.services2 {
  padding: 0 0 120px 0;
  &__games-mob {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 25px;
    margin-top: 25px;
    display: none;
    .services2__game--active {
      background: transparent;
      .services2__game-title2 {
        color: #ae7cff;
      }
    }
  }
  &__game-mob {
    flex-direction: column;
    text-align: center;
    padding: 0 !important;
    &:hover {
      background: transparent !important;
      .services2__game-title2 {
        color: #ae7cff;
      }
    }
    .services2__game-img2 {
      width: 100%;
      img {
        width: 100%;
      }
    }
    .services2__game-title2 {
      font-size: 18px;
      margin-top: 15px;
      br {
        display: none;
      }
    }
  }
  &__burger {
    width: 32px;
    height: 22px;
    position: relative;
    margin-left: 10px;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #ffffff;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
  }
  &__left {
    margin-right: 28px;
   &-wrapp {
     width: 387px;
     background: #1F1F31;
     border-radius: 15px;
     padding: 30px 15px 40px;
     position: relative;
     overflow-y: auto;
     &::-webkit-scrollbar {
       width: 3px;
     }
     &::-webkit-scrollbar-track {
       background: #ffffff;
       border-radius: 3px;
     }
     &::-webkit-scrollbar-thumb {
       background: #1F1F31;
       border-radius: 3px;
     }
   }
    &-close {
      position: absolute;
      top: 20px;
      right: 20px;
      width: 32px;
      padding: 5px;
      cursor: pointer;
      display: none;
      img {
        width: 100%;
      }
    }
    &-title {
      font-size: 16px;
      line-height: 120%;
      color: #9B9BB7;
    }
  }
  &__right {
    width: calc(100% - 415px);
    justify-content: space-between;
    display: none;
    &--active {
      display: flex;
    }
  }
  &__games {
    margin-top: 30px;
  }
  &__game {
    display: flex;
    padding: 15px;
    cursor: pointer;
    &--active {
      background: #323248;
      border-radius: 10px;
    }
    &:hover {
      background: #323248;
      border-radius: 10px;
    }
    &-img {
      width: 100px;
      img {
        width: 100%;
        height: auto;
      }
    }
    &-desc {
      margin-left: 20px;
    }
    &-title {
      font-family: 'Neue Einstellung';
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
    }
    &-price {
      display: flex;
      align-items: center;
      margin-top: 10px;
    }
    &-info {
      display: inline-block;
      font-size: 14px;
      line-height: 105%;
      margin-top: 15px;
      padding: 4px 8px;
      background: #9039FF;
      border-radius: 5px;
    }
  }
  &__price-text {
    font-size: 18px;
    line-height: 105%;
    margin-left: 4px;
    span {
      font-size: 14px;
      line-height: 120%;
      color: #9696AE;

    }
  }
  &-info {
    width: calc(100% - 415px);
    &__game {
      background: #1F1F31;
      border-radius: 15px;
      padding: 25px;
    }
    &__game-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__game-title {
      font-family: 'Neue Einstellung';
      font-weight: 700;
      font-size: 36px;
      line-height: 120%;
    }
    &__game-rating {
      display: flex;
      align-items: center;
      flex-shrink: 0;
      background: #161628;
      border-radius: 30px;
      padding: 7px 15px;
      margin-left: 15px;
    }
    &__rating-desc {
      display: flex;
      align-items: center;
      font-weight: 500;
      font-size: 24px;
      line-height: 105%;
      margin-left: 12px;
      span {
        font-size: 20px;
        line-height: 120%;
        color: #9696AE;
        margin-left: 8px;
      }
    }
    &__game-img {
      margin-top: 20px;
      img {
        width: 100%;
        height: auto;
      }
    }
    &__game-desc {
      margin-top: 30px;
    }
    &__game-text {
      font-size: 16px;
      line-height: 140%;
      color: #9696AE;
    }
    &__game-items {
      display: inline-grid;
      grid-template-columns: 0.5fr 1fr;
      gap: 25px 100px;
      margin-top: 25px;
    }
    &__game-item {
      font-size: 16px;
      line-height: 120%;
      &:nth-child(odd) {
        color: #9696AE;
      }
    }
  }
  &__types {
    width: 387px;
    margin-left: 28px;
    &-img img {
      width: 100%;
      height: auto;
    }
    &-content {
      background: #1F1F31;
      border-radius: 15px;
      margin-top: 30px;
      padding: 35px 25px 40px;
    }
    &-title {
      font-weight: 500;
      font-size: 22px;
      line-height: 120%;
      margin-bottom: 20px;
    }
    &-btns {
      background: #1F1F31;
      border-radius: 15px;
      margin-top: 30px;
      padding: 30px 40px;
    }
    &-btn {
      width: 100%;
      height: 50px;
      font-size: 16px;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
    &-btn2 {
      background: #FF8A00;
      border-radius: 60px;
      border: 1px solid #FF8A00;
      color: #FFFFFF;
      &:hover {
        background: transparent;
        color: #FF8A00;
      }
    }
    .btn-border {
      background: transparent;
      &:hover {
        background: #AE7CFF;
        color: #ffffff;
      }
    }
  }
  &__reservation {
    background: #1F1F31;
    border: 1px solid #9039FF;
    border-radius: 15px;
    padding: 20px 20px 30px;
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    &-title {
      font-size: 24px;
      line-height: 120%;
      color: #BD95FF;
    }
    &-amount {
      display: flex;
      align-items: center;
      margin-top: 5px;
      span {
        font-size: 24px;
        line-height: 120%;
        color: #BD95FF;
        margin-left: 3px;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #F5F5F5;
      margin-top: 10px;
    }
  }
  &__type {
    background: #323248;
    border-radius: 10px;
    padding: 20px;
    cursor: pointer;
    &:not(:first-child) {
      margin-top: 17px;
    }
    &-title {
      font-weight: 500;
      font-size: 16px;
      line-height: 140%;
    }
  }
  &-estimation {
    background: #1F1F31;
    border-radius: 15px;
    margin-top: 60px;
    padding: 40px 30px 30px;
    &__top {
      display: flex;
      align-items: center;
    }
    &__title {
      font-weight: 500;
      font-size: 24px;
      line-height: 120%;
      margin-left: 30px;
    }
    &__rating {
      font-family: 'Neue Einstellung';
      font-weight: 500;
      font-size: 24px;
      line-height: 100%;
      display: flex;
      align-items: center;
      img {
        margin-top: -3px;
      }
      span {
        margin-top: 5px;
        margin-left: 12px;
      }
    }
    &__items {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 22px;
      margin-top: 40px;
      padding-bottom: 35px;
    }
    &__item {
      display: flex;
      align-items: center;
      &-title {
        min-width: 126px;
        font-size: 18px;
        line-height: 120%;
        display: flex;
        align-items: center;
        margin-right: 20px;
      }
      &-rating {
        font-size: 18px;
        line-height: 120%;
        padding-left: 150px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: -20px;
          transform: translate(0, -50%);
          width: 140px;
          height: 4px;
          background: #FFA400;
          border-radius: 3px;
          margin-left: 20px;
        }
      }
    }
    &__user {
      display: flex;
      align-items: center;
      padding: 30px 0;
      border-top: 1px solid #45456D;
      &:last-child {
        border-bottom: 1px solid #45456D;
      }
      &-img {
        width: 50px;
        img {
          width: 100%;
        }
      }
      &-desc {
        margin-left: 20px;
      }
      &-title {
        display: flex;
        align-items: center;
      }
      &-name {
        font-size: 24px;
        line-height: 120%;
      }
      &-rating {
        font-family: 'Neue Einstellung';
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        margin-left: 20px;
        span {
          margin-left: 5px;
        }
      }
      &-info {
        font-size: 14px;
        line-height: 120%;
        color: #9696AE;
        margin-top: 5px;
      }
    }
    &__btn {
      font-size: 14px;
      line-height: 120%;
      color: #AE7CFF;
      background: transparent;
      padding: 0;
      margin: 30px auto 0;
    }
  }
  &-basket {
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    transition: all 0.5s;
    opacity: 0;
    pointer-events: none;
    &--active {
     opacity: 1;
      pointer-events: all;
      .services2-basket__wrapp {
        right: 0;
      }
    }
    &__wrapp {
      position: fixed;
      top: 0;
      right: -100%;
      width: 960px;
      height: 100%;
      background: #1F1F31;
      padding: 30px;
      transition: all 0.5s;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #ffffff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #1F1F31;
        border-radius: 3px;
      }
    }
    &__top {
      display: flex;
      align-items: center;
    }
    &__close {
      width: 50px;
      height: 50px;
      background: #323248;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      cursor: pointer;
      &:hover {
        background: #4e4e5f;
      }
      img {
        max-width: 17px;
        transform: rotate(180deg);
      }
    }
    &__title {
      font-weight: 500;
      font-size: 24px;
      line-height: 120%;
      margin-left: 20px;
    }
    &__choice {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;
      padding-bottom: 30px;
    }
    &__img {
      img {
        width: 100%;
        border-radius: 15px;
      }
    }
    &__desc {
      max-width: 575px;
      width: 100%;
      margin-left: 20px;
    }
    &__subtitle {
      font-family: 'Neue Einstellung';
      font-weight: 700;
      font-size: 20px;
      line-height: 120%;
    }
    .select2 {
      display: none;
    }
    &__select-title {
      font-size: 16px;
      line-height: 120%;
      margin-top: 20px;
    }
    &__calc {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 15px;
      &-title {
        margin-top: 27px;
      }
      input {
        max-width: 456px;
        width: 100%;
        height: 50px;
        background: #323248;
        border-radius: 7px;
        border: 1px solid transparent;
        font-size: 16px;
        line-height: 120%;
        color: #ffffff;
        padding: 0 20px;
      }
      &-btns {
        display: flex;
        align-items: center;
        margin-left: 10px;
      }
    }
    &__plus {
      width: 50px;
      height: 50px;
      background: #323248;
      border-radius: 7px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 10px;
      cursor: pointer;
      img {
        max-width: 24px;
      }
      &:hover {
        background: #4e4e5f;
      }
    }
    &__minus {
      width: 50px;
      height: 50px;
      background: #323248;
      border-radius: 7px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      img {
        max-width: 24px;
      }
      &:hover {
        background: #4e4e5f;
      }
    }
    &__results {
      padding: 15px 0 240px;
      border-top: 1px solid #45456D;
      border-bottom: 1px solid #45456D;
    }
    &__amount {
      display: flex;
      justify-content: space-between;
    }
    &__amount-title {
      font-size: 16px;
      line-height: 120%;
      color: #9696AE;
    }
    &__amount-desc {
      display: flex;
      align-items: center;
      span {
        font-weight: 600;
        font-size: 16px;
        line-height: 120%;
        margin-left: 3px;
      }
    }
    &__discount {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 15px;
    }
    &__discount-title {
      font-size: 16px;
      line-height: 120%;
      color: #9696AE;
    }
    &__discount-desc {
      display: flex;
      align-items: center;
      span:nth-child(1) {
        font-size: 12px;
        line-height: 120%;
        background: #9039FF;
        border-radius: 5px;
        padding: 5px;
      }
      span:nth-child(2) {
        font-size: 16px;
        line-height: 120%;
        margin-left: 10px;
      }
    }
    &__result {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 40px;
    }
    &__result-title {
      font-size: 16px;
      line-height: 120%;
      color: #9696AE;
    }
    &__result-desc {
      display: flex;
      align-items: center;
    }
    &__result-unit {
      font-size: 16px;
      line-height: 120%;
    }
    &__result-price {
      display: flex;
      align-items: center;
      margin-left: 7px;
      span {
        font-weight: 600;
        font-size: 16px;
        line-height: 120%;
        margin-left: 3px;
      }
    }
    &__bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-top: 30px;
    }
    &__bottom-desc {
      display: flex;
      align-items: center;
    }
    &__bottom-unit {
      font-size: 16px;
      line-height: 120%;
    }
    &__bottom-price {
      display: flex;
      align-items: center;
      margin-left: 7px;
      span {
        font-weight: 600;
        font-size: 24px;
        line-height: 120%;
        margin-left: 3px;
      }
    }
    &__bottom-btns {
      display: flex;
      align-items: center;
    }
    &__bottom-btn {
      background: #323248;
      border-radius: 60px;
      line-height: 120%;
      color: #ffffff;
      &:hover {
        background: #6333b1;
      }
    }
    &__bottom-btn,
    &__bottom-btn2 {
      width: 180px;
      height: 60px;
      font-size: 16px;
    }
    &__bottom-btn2 {
      margin-left: 20px;
    }
  }
  .select {
    &__wrap {
      position: relative;
      margin-top: 15px;
      &--active {
        .select__list {
          display: block;
        }
      }
    }
    &__list-default {
      width: 100%;
      height: 50px;
      background: #323248;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
      border-radius: 7px;
      display: flex;
      align-items: center;
      padding: 0 35px 0 20px;
      position: relative;
      cursor: pointer;
      li {
        width: 100%;
      }
    }
    &__list {
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      background: #323248;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
      border-radius: 7px;
      display: none;
      li {
        height: 50px;
        display: flex;
        align-items: center;
        padding: 0 20px;
        cursor: pointer;
        &:hover {
          background: #49495F;
        }
        &:first-child:hover {
          border-radius: 7px 7px 0 0;
        }
        &:last-child:hover {
          border-radius: 0 0 7px 7px;
        }
      }
      .li--active {
        background: #49495F;
        &:first-child {
          border-radius: 7px 7px 0 0;
        }
        &:last-child {
          border-radius: 0 0 7px 7px;
        }
      }
    }
    &-arrow {
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translate(0, -50%);
    }
    &__desc {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__title {
      font-size: 16px;
      line-height: 120%;
    }
    &__text {
      display: flex;
      align-items: center;
    }
    &__price {
      display: flex;
      align-items: center;
    }
    &__price-desc {
      font-size: 18px;
      line-height: 120%;
      margin-left: 4px;
      span {
        font-size: 14px;
        line-height: 120%;
      }
    }
    &__info {
      display: inline-block;
      height: 24px;
      background: #9039FF;
      border-radius: 5px;
      font-size: 12px;
      line-height: 120%;
      padding: 5px;
      margin-left: 10px;
    }
    &__arrow {
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translate(0, -50%);
    }
  }
}

@media screen and (max-width: 1620px) {
  .services2 {
    &-estimation__item {
      &-rating {
        padding-left: 110px;
        &:after {
          width: 100px;
        }
      }
    }
  }
}

@media screen and (max-width: 1550px) {
  .services2 {
    &-estimation__items {
      grid-template-columns: 1fr;
    }
  }
}

@media screen and (max-width: 1400px) {
  .services2 {
    &__games-mob {
      display: grid;
    }
    &__left {
      display: none;
    }
    &__right {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1200px) {
  .services2-basket {
    &__wrapp {
      width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .services2-top {
    &__logo {
      &-img {
        width: 100px;
      }
      &-desc {
        margin-left: 15px;
      }
      &-name {
        font-size: 24px;
      }
    }
  }
  .services2 {
    &-info {
      width: calc(100% - 328px);
      &__game-items {
        gap: 25px;
      }
      &__game-title {
        font-size: 28px;
      }
    }
    &__types {
      width: 300px;
    }
    &-basket {
      &__choice {
        display: block;
      }
      &__img {
        max-width: 400px;
      }
      &__desc {
        max-width: 100%;
        margin: 20px 0 0 0;
      }
      &__calc {
       input {
         max-width: 680px;
       }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .services2-top {
    padding: 40px 0;
  }
  .services2 {
    position: relative;
    padding-top: 110px;
    &__game-mob {
      padding: 5px;
      .services2__game-title2 {
        font-size: 12px;
        margin-top: 7px;
      }
    }
    &--two {
      padding-top: 175px;
    }
    &__title {
      margin-top: 28px;
    }
    &__menu-btn {
      font-size: 24px;
    }
    &__right {
      &--active {
        flex-direction: column;
      }
    }
    &-info {
      width: 100%;
      margin-top: 28px;
      &__game-title {
        font-size: 24px;
      }
      &__rating-desc {
        font-size: 20px;
      }
    }
    &__types {
      width: 100%;
      margin: 0;
      order: -1;
      display: flex;
      flex-direction: column;
      &-title {
        font-size: 20px;
      }
      &-content {
        order: -1;
        margin: 0;
      }
      &-img {
        display: none;
      }
      &-btns {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        margin-top: 0;
      }
    }
    &-estimation__rating {
      font-size: 20px;
    }
    &-estimation__title {
      font-size: 20px;
    }
    &-basket {
      &__bottom {
        display: block;
      }
      &__bottom-price {
        span {
          font-size: 20px;
        }
      }
      &__bottom-btns {
        margin: 15px 0 0 0;
      }
    }
    &__reservation {
      order: -2;
      margin: 0 0 28px 0;
      &-title {
        font-size: 20px;
      }
      &-amount {
        span {
          font-size: 20px;
        }
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .services2 {
    .select {
      &__list-default {
        height: 100%;
        min-height: 75px;
      }
      &__list li {
        height: 100%;
        min-height: 75px;
      }
      &__desc {
        display: block;
      }
      &__text {
        margin-top: 10px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .services2-top {
    &__inner {
     flex-direction: column;
    }
    &__logo {
      max-width: 400px;
      margin: 0 auto;
      &-img {
        width: 100%;
      }
      &-desc {
        flex-shrink: 0;
      }
    }
    &__socials {
      order: -1;
      justify-content: center;
      margin-bottom: 20px;
    }
  }
  .services2 {
    padding: 110px 0 40px 0;
    &--two {
      padding: 175px 0 40px 0;
    }
    &__games-mob {
      gap: 10px;
    }
    &__game-mob {
      .services2__game-title2 {
        br {
          display: block;
        }
      }
    }
    &__left-wrapp {
      width: 100%;
    }
    &-estimation {
      padding: 40px 15px 30px;
      &__item {
        justify-content: space-between;
      }
      &__user {
        position: relative;
        padding: 30px 70px 30px 0;
      }
      &__user-rating {
        position: absolute;
        top: 30px;
        right: 15px;
        margin-left: 0;
      }
    }
    &-basket {
      &__results {
        padding: 15px 0 30px;
      }
      &__amount {
        display: block;
      }
      &__amount-desc {
        margin-top: 10px;
      }
      &__discount {
        display: block;
        margin-top: 25px;
      }
      &__discount-desc {
        margin-top: 10px;
      }
      &__result {
        display: block;
        margin-top: 70px;
      }
      &__result-desc {
        margin-top: 10px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .services2 {
    padding-top: 50px;
    &--two {
      padding-top: 115px;
    }
    &__games-mob {
      .services2__game-title2 {
        font-size: 10px;
      }
    }
    &-info {
      &__game-top {
        display: block;
      }
      &__game-rating {
        display: inline-flex;
        margin: 15px 0 0 0;
      }
    }
    &__types-btns {
      width: calc(100% - 30px);
      left: 50%;
      transform: translate(-50%, 0);
      padding: 0;
      background: transparent;
    }
    .select {
      &__list-default {
        min-height: 120px;
        padding: 10px 35px 10px 20px;
      }
      &__list li {
        min-height: 120px;
        padding: 10px 20px;
      }
      &__text {
        display: block;
      }
      &__info {
        margin: 10px 0 0 0;
      }
    }
    &-basket {
      &__bottom-btn {
        height: 40px;
      }
      &__bottom-btn2 {
        height: 40px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .services2 {
    &-info {
      &__game-text {
        font-size: 14px;
      }
      &__game-items {
        grid-template-columns: 1fr;
        gap: 12px;
      }
    }
    &-estimation {
      &__item-rating {
        padding-left: 60px;
        &:after {
          width: 50px;
        }
      }
    }
  }
}
