.eplay-main {
  background: #5C33CF;
  padding: 40px 0 60px;
  &__inner {
    text-align: center;
  }
  &__title {
    font-family: 'Earths Mightiest';
    font-weight: 700;
    font-size: 75px;
    line-height: 120%;
  }
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    li {
      font-size: 24px;
      line-height: 120%;
      font-weight: 500;
      position: relative;
      &:not(:last-child) {
        margin-right: 30px;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: -15px;
          transform: translate(0, -50%);
          width: 3px;
          height: 3px;
          background: #ffffff;
          border-radius: 50%;
        }
      }
    }
  }
  &__btn {
    width: 292px;
    height: 60px;
    background: #242440;
    border-radius: 60px;
    font-size: 16px;
    line-height: 120%;
    color: #FFFFFF;
    margin: 40px auto 0;
    border: 2px solid #242440;
    &:hover {
      background: transparent;
    }
  }
}
.eplay-info {
  padding: 40px 0 0 0;
  overflow: hidden;
  &__inner {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__left {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 500px;
      height: 500px;
      border-radius: 50%;
      background: radial-gradient(50% 50% at 50% 50%, rgba(92, 51, 207, 0.5) 0%, rgba(223, 24, 255, 0) 100%);
      pointer-events: none;
    }
  }
  &__right {
    margin-left: 100px;
  }
  &__img {
    width: 413px;
    position: relative;
    z-index: 1;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__desc {
    font-size: 16px;
    line-height: 120%;
    color: #D9D9D9;
    margin-top: 15px;
  }
}
.eplay-advantages {
  background: #1F1F31;
  padding: 54px 0 72px;
  &__title {
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 50px;
    max-width: 1140px;
    margin: 50px auto 0;
  }
  &__item {
    text-align: center;
    &-img {
      img {
        width: auto;
        height: auto;
      }
    }
    &-title {
      font-weight: 500;
      font-size: 20px;
      line-height: 120%;
      margin-top: 33px;
    }
    &-desc {
      font-size: 16px;
      line-height: 120%;
      color: #D9D9D9;
      margin-top: 10px;
    }
  }
}
.eplay-info2 {
  padding: 65px 0 0 0;
  overflow: hidden;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    padding-bottom: 30px;
  }
  &__right {
    position: relative;
    margin-left: 50px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 564px;
      height: 564px;
      background: radial-gradient(50% 50% at 50% 50%, rgba(57, 160, 255, 0.5) 0%, rgba(152, 71, 254, 0) 100%);
      border-radius: 50%;
      pointer-events: none;
    }
  }
  &__list {
    margin-top: 30px;
    li {
      font-size: 16px;
      line-height: 140%;
      color: #D9D9D9;
      &:not(:first-child) {
        margin-top: 10px;
      }
    }
  }
  &__desc {
    font-size: 16px;
    line-height: 130%;
    color: #D9D9D9;
    margin-top: 30px;
    a {
      color: #9039FF;
    }
  }
  &__img {
    position: relative;
    z-index: 1;
    width: 516px;
    img {
      width: 100%;
      height: auto;
    }
  }
}
.eplay-about {
  background: #1F1F31;
  padding: 57px 0 119px;
  &__inner {
    text-align: center;
  }
  &__desc {
    font-size: 16px;
    line-height: 140%;
    color: #D9D9D9;
    max-width: 742px;
    margin: 23px auto 0;
  }
  &__btn {
    width: 292px;
    height: 60px;
    margin: 75px auto 0;
  }
}

@media screen and (max-width: 1200px) {
  .eplay-info2 {
    &__img {
      width: 400px;
    }
  }
}

@media screen and (max-width: 992px) {
  .eplay-info {
    &__right {
      margin-left: 50px;
    }
    &__img {
      width: 350px;
    }
  }
  .eplay-advantages {
    &__items {
      gap: 30px;
    }
  }
  .eplay-info2 {
    &__img {
      width: 350px;
    }
  }
}

@media screen and (max-width: 768px) {
  .eplay-main {
    &__title {
      font-size: 48px;
    }
    &__list {
      li {
        font-size: 18px;
      }
    }
  }
  .eplay-info {
    &__right {
      margin-left: 30px;
    }
    &__img {
      width: 300px;
    }
  }
  .eplay-advantages {
    &__items {
      margin-top: 40px;
    }
    &__item {
      &-img {
        max-width: 60px;
        height: 90px;
        margin: 0 auto;
        display: flex;
        align-items: flex-end;
        img {
          width: 100%;
        }
      }
    }
  }
  .eplay-info2 {
    &__inner {
      display: block;
    }
    &__left {
      padding-bottom: 0;
    }
    &__right {
      margin: 50px 0 0 0;
    }
    &__img {
      width: 300px;
    }
  }
}

@media screen and (max-width: 576px) {
  .eplay-info {
    padding: 40px 0;
    &__inner {
      display: block;
    }
    &__right {
      margin: 50px 0 0 0;
    }
    &__img {
      margin: 0 auto;
    }
  }
  .eplay-advantages {
    &__items {
      grid-template-columns: 1fr;
      gap: 50px;
    }
  }
  .eplay-about {
    padding: 40px 0 60px;
    &__btn {
      width: 100%;
      margin: 40px auto 0;
    }
  }
}

@media screen and (max-width: 450px) {
  .eplay-main {
    &__list {
      flex-direction: column;
      li {
        &:not(:last-child) {
          margin: 0 0 25px 0;
          &:after {
            display: none;
          }
        }
      }
    }
    &__btn {
      width: 100%;
    }
  }
}