.discount-main {
  background: #5C33CF;
  &__inner {
    text-align: center;
    padding: 40px 0 110px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 400px;
      height: 400px;
      background: radial-gradient(50% 50% at 50% 50%, #FF4773 0%, rgba(92, 51, 207, 0) 100%);
    }
  }
  &__title {
    font-family: 'Earths Mightiest';
    font-weight: 700;
    font-size: 75px;
    line-height: 120%;
  }
  &__subtitle {
    font-weight: 500;
    font-size: 24px;
    line-height: 120%;
    margin-top: 22px;
  }
  &__img {
    position: absolute;
    z-index: 1;
    top: 15px;
    right: 0;
    width: 370px;
    img {
      width: 100%;
    }
  }
}
.discount {
  padding: 80px 0;
  &__inner {
    max-width: 820px;
    margin: 0 auto;
    text-align: center;
  }
  &__desc {
    margin-top: 45px;
    p {
      font-size: 20px;
      line-height: 130%;
      color: #D9D9D9;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr;
    gap: 10px;
    margin-top: 50px;
  }
  &__item {
    height: 60px;
    background: #1F1F31;
    border-radius: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    &-amount {
      display: flex;
      align-items: center;
    }
    &-percent {
      font-weight: 500;
      font-size: 24px;
      line-height: 120%;
      color: #AE7CFF;
      margin-left: 15px;
    }
  }
  &__amount-from,
  &__amount-before {
    display: flex;
    align-items: center;
    span {
      font-weight: 500;
      font-size: 18px;
      line-height: 120%;
      margin-left: 5px;
    }
  }
  &__amount-before {
    margin-left: 30px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: -19px;
      transform: translate(0, -50%);
      width: 7px;
      height: 3px;
      background: #ffffff;
    }
  }
}

@media screen and (max-width: 1400px) {
  .discount-main {
    &__inner {
      &:after {
        width: 300px;
        height: 300px;
      }
    }
    &__img {
      width: 300px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .discount-main {
    &__inner {
      &:after {
       display: none;
      }
    }
    &__img {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .discount-main {
    &__inner {
      padding: 20px 0 40px 0;
    }
    &__title {
      font-size: 48px;
    }
    &__subtitle {
      font-size: 20px;
      margin-top: 10px;
    }
  }
  .discount {
    padding: 40px 0;
    &__desc {
      margin-top: 30px;
      p {
        font-size: 16px;
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
    &__items {
      margin-top: 30px;
    }
    &__item {
      padding: 0 15px;
     &-percent {
       font-size: 16px;
     }
    }
    &__amount-from,
    &__amount-before {
      span {
        font-size: 16px;
      }
    }
    &__amount-before {
      &:after {
        height: 2px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .discount {
    &__desc {
      p {
        font-size: 14px;
      }
    }
    &__item {
      &-percent {
        font-size: 14px;
      }
    }
    &__amount-from,
    &__amount-before {
      span {
        font-size: 14px;
      }
    }
  }
}