.services {
  padding: 90px 0 115px;
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 28px;
    margin-top: 40px;
  }
  &__item {
   &-radio {
     position: relative;
   }
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
      &:checked~label {
        border: 1px solid #9039FF;
      }
      &:checked~.services__item-check {
        opacity: 1;
        pointer-events: all;
      }
    }
    label {
      width: 100%;
      height: 100%;
      border: 1px solid transparent;
      border-radius: 15px;
      display: block;
      img {
        border-radius: 15px;
        width: 100%;
        height: auto;
      }
    }
    &-check {
      position: absolute;
      top: 20px;
      right: 20px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
    }
    &-title {
      font-weight: 500;
      font-size: 18px;
      line-height: 120%;
      margin-top: 25px;
      text-align: center;
    }
  }
  &__bottom {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 175px;
  }
  &__name {
    display: flex;
    align-items: center;
    margin-right: 40px;
    span:nth-child(1) {
      font-size: 18px;
      line-height: 120%;
      color: #D9D9D9;
    }
  }
  .game {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    display: none;
    &--active {
      display: block;
    }
  }
  &-step__wrapp {
    max-width: 700px;
    margin: 0 auto;
  }
  &-step__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &-step2 {
    &__info {
      margin-top: 20px;
      background: #1F1F31;
      border-radius: 15px;
      padding: 30px 35px 70px;
    }
    &__subtitle {
      font-weight: 500;
      font-size: 20px;
      line-height: 120%;
    }
    &__items {
      margin-top: 35px;
    }
    &__item {
      &:not(:first-child) {
        margin-top: 28px;
      }
      &-subtitle {
        font-size: 16px;
        line-height: 120%;
        margin-bottom: 10px;
      }
    }
    &__file {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__file-input {
      position: relative;
      input {
       position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        cursor: pointer;
        opacity: 0;
      }
      label {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        flex-direction: column;
        width: 315px;
        height: 198px;
        background: #323248;
        border-radius: 7px;
        padding: 15px;
        font-size: 14px;
        line-height: 120%;
        color: #7D7D97;
        img {
          width: 82px;
          height: auto;
        }
        span {
          margin-top: 18px;
        }
      }
    }
    &__file-desc {
      font-size: 12px;
      line-height: 120%;
      color: #7D7D97;
      margin-left: 20px;
    }
  }
  &-step3 {
    &__info {
      margin-top: 20px;
      background: #1F1F31;
      border-radius: 15px;
      padding: 30px 35px 70px;
    }
    &__subtitle {
      font-weight: 500;
      font-size: 20px;
      line-height: 120%;
    }
    &__items {
      margin-top: 35px;
    }
    &__item {
      position: relative;
      &:not(:first-child) {
        margin-top: 28px;
      }
      &-subtitle {
        font-size: 16px;
        line-height: 120%;
        margin-bottom: 10px;
      }
      &-info {
        font-size: 12px;
        line-height: 120%;
        color: #7D7D97;
        margin-top: 8px;
      }
    }
    &__textarea2 {
      width: 100%;
      height: 50px;
      background: #323248;
      border-radius: 7px;
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      margin: 0;
      padding: 15px 20px;
      border: none;
      resize: none;
      overflow-y: auto;
      &::placeholder {
        color: #9B9BB7;
      }
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #ffffff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #323248;
        border-radius: 3px;
      }
      &::placeholder {
        font-size: 15px;
        color: #9B9BB7;
      }
    }
    &__textarea {
      width: 100%;
      height: 142px;
      background: #323248;
      border-radius: 7px;
      padding: 15px 20px 40px 20px;
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      border: none;
      resize: none;
      overflow-y: auto;
      &::placeholder {
        color: #9B9BB7;
      }
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #ffffff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #323248;
        border-radius: 3px;
      }
    }
    &__symbols {
      position: absolute;
      right: 20px;
      bottom: 20px;
      display: flex;
      align-items: center;
      span {
        font-size: 14px;
        line-height: 120%;
        color: #7D7D97;
      }
    }
  }
  &-step4 {
    &__info {
      margin-top: 20px;
      background: #1F1F31;
      border-radius: 15px;
      padding: 30px 35px 30px;
    }
    &__subtitle {
      font-weight: 500;
      font-size: 20px;
      line-height: 120%;
    }
    &__items {
      margin-top: 35px;
    }
    &__item {
      position: relative;
      &:not(:first-child) {
        margin-top: 28px;
      }
      &-subtitle {
        font-size: 16px;
        line-height: 120%;
        margin-bottom: 10px;
      }
      &-input {
        position: relative;
        input {
          width: 100%;
          height: 50px;
          background: #323248;
          border-radius: 7px;
          color: #ffffff;
          border: unset;
          padding: 0 20px;
          font-size: 16px;
          line-height: 120%;
        }
        span {
          position: absolute;
          top: 50%;
          right: 20px;
          transform: translate(0, -50%);
          font-size: 14px;
          line-height: 120%;
          color: #7D7D97;
        }
      }
      &-desc {
        display: block;
        font-size: 12px;
        line-height: 120%;
        color: #7D7D97;
        margin-top: 15px;
      }
    }
    &__top {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__close {
      width: 32px;
      height: 32px;
      background: #323248;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
    }
    &__textarea {
      width: 100%;
      height: 50px;
      background: #323248;
      border-radius: 7px;
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      margin: 0;
      padding: 15px 20px;
      border: none;
      resize: none;
      overflow-y: auto;
      &::placeholder {
        color: #9B9BB7;
      }
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #ffffff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #323248;
        border-radius: 3px;
      }
      &::placeholder {
        font-size: 16px;
        color: #9B9BB7;
      }
    }
    &__item-flex {
     display: flex;
     align-items: center;
    }
    &__item-flex2 {
      display: flex;
      align-items: center;
    }
    .input-price {
      width: calc(100% - 230px);
      height: 50px;
      background: #323248;
      border-radius: 7px;
      padding: 0 20px 0 50px;
      font-size: 16px;
      line-height: 120%;
      color: #9B9BB7;
      background-image: url("../img/price-icon.svg");
      background-repeat: no-repeat;
      background-position: left 20px top 50%;
      border: 1px solid transparent;
    }
    .select2 {
      display: none;
    }
    &__select {
      height: 50px;
      margin-left: 10px;
    }
    .nice-select {
      min-width: 220px;
      height: 50px;
      border-radius: 7px
    }
    .check-title {
      margin-bottom: 0;
    }
    &__check {
      position: relative;
      margin-left: 10px;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        border: none;
        padding: 0;
        opacity: 0;
        cursor: pointer;
        &:checked~label {
          background: #9039FF;
          &:after {
           left: 24px;
          }
        }
      }
      label {
        display: block;
        width: 44px;
        height: 24px;
        background: #535370;
        border-radius: 30px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 4px;
          transform: translate(0, -50%);
          width: 16px;
          height: 16px;
          background: #FFFFFF;
          box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
          border-radius: 50%;
          transition: all 0.5s;
        }
      }
    }
    &__select2,
    &__select3,
    &__select4 {
      height: 50px;
      margin-top: 15px;
      pointer-events: none;
      .current {
        color: #9B9BB7;
      }
      &--active {
        pointer-events: all;
        .current {
          color: #ffffff;
        }
      }
    }
    &__price {
      &-flex {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      &-title {
        font-size: 16px;
        line-height: 120%;
      }
      &-desc {
        display: flex;
        align-items: center;
      }
      &-numbers {
        font-size: 28px;
        line-height: 100%;
        display: flex;
        align-items: center;
        margin-left: 3px;
        span {
          font-size: 18px;
        }
      }
    }
    &__btn {
      width: 100%;
      height: 80px;
      background: #1F1F31;
      border-radius: 15px;
      font-weight: 500;
      font-size: 20px;
      line-height: 120%;
      color: #AE7CFF;
      margin-top: 50px;
      border: 3px solid #1F1F31;
      &:hover {
        background: transparent;
      }
    }
  }
}
.step {
  display: none;
  &--active {
    display: block;
  }
}
.progressbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  max-width: 68px;
  width: 100%;
}
.progress-step {
  width: 14px;
  height: 4px;
  background: #323248;
  border-radius: 2px;
  &--active {
    background: #ffffff;
  }
}
.services-quiz__btns {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 50px;
}
.prev-quiz {
  width: 200px;
  height: 60px;
  background: #323248;
  border-radius: 60px;
  &:hover {
    background: #6333B1;
  }
  span {
    font-size: 16px;
    line-height: 120%;
    color: #FFFFFF;
    margin-left: 9px;
  }
}
.next-quiz {
  width: 200px;
  height: 60px;
  span {
    margin-right: 9px;
  }
  &2 {
    width: 200px;
    height: 60px;
    span {
      margin-right: 9px;
    }
  }
}

@media screen and (max-width: 992px) {
  .services {
    &__items {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
}

@media screen and (max-width: 768px) {
  .services {
    padding: 40px 0;
    &__items {
      gap: 15px;
    }
    &-step2 {
      &__file-input {
        label {
          width: 200px;
          height: 120px;
          font-size: 12px;
          img {
            width: 50px;
          }
        }
      }
    }
    &__bottom {
      margin-top: 100px;
    }
  }
}

@media screen and (max-width: 600px) {
  .services {
    &-step4 {
      .input-price {
        width: 100%;
      }
      &__item-flex {
        display: block;
        margin-top: 15px;
      }
      &__select {
        margin: 15px 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .services {
    &__items {
      grid-template-columns: 1fr 1fr;
    }
    &__bottom {
      justify-content: start;
    }
    &__name {
      display: block;
      margin-right: 20px;
    }
  }
  .prev-quiz,
  .next-quiz,
  .next-quiz2{
    width: 120px;
    height: 40px;
  }
}

@media screen and (max-width: 450px) {
  .services {
    &-step2 {
      &__info {
        padding: 30px 15px 70px;
      }
      &__file {
        display: block;
      }
      &__file-desc {
        margin: 20px 0 0 0;
      }
    }
    &-step3 {
      &__info {
        padding: 30px 15px 70px;
      }
      &__item-info {
        font-size: 10px;
      }
      &__textarea2{
        font-size: 12px;
        &::placeholder {
          font-size: 12px;
        }
      }
      &__textarea {
        font-size: 12px;
      }
      &__symbols {
        span {
          font-size: 12px;
        }
      }
    }
    &-step4 {
      &__info {
        padding: 30px 15px 30px;
      }
      &__textarea {
        font-size: 12px;
      }
      .input-price {
        font-size: 12px;
      }
      &__item-input {
        input {
          font-size: 12px;
        }
        span {
          font-size: 12px;
        }
      }
      &__select,
      &__select2,
      &__select3 {
        .current {
          font-size: 12px;
        }
        .list li {
          font-size: 12px;
        }
      }
      &__select4 {
        .current {
          font-size: 12px;
        }
        .list li {
          font-size: 12px;
        }
      }
      &__price {
       &-flex {
         display: block;
       }
        &-desc {
          margin-top: 10px;
        }
        &-numbers {
          font-size: 20px;
          span {
            font-size: 14px;
          }
        }
      }
      &__btn {
        font-size: 14px;
      }
      &__textarea {
        padding: 10px 20px;
      }
    }
  }
}

@media screen and (max-width: 360px) {
  .services {
    &__items {
      grid-template-columns: 1fr;
    }
  }
}