}
.navigation {
  padding: 50px;
  &__list li {
    font-size: 24px;
    line-height: 120%;
    &:not(:first-child) {
      margin-top: 25px;
    }
  }
  &__btn {
    font-size: 24px;
    width: 250px;
    height: 50px;
    margin-top: 25px;
  }
}
.nice-select {
  min-width: 160px;
  width: 100%;
  height: 34px;
  background: #323248 !important;
  border: unset;
  border-radius: 60px;
  display: flex;
  align-items: center;
  padding: 0 30px 0 12px;
  &:after {
    z-index: 1;
    top: 50%;
    right: 12px;
    transform: translate(0, -50%);
    width: 10px;
    height: 6px;
    background-image: url("../img/arrow-select.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  .current {
    font-family: 'Rubik';
    font-weight: 400;
    font-size: 16px;
    line-height: 105%;
    color: #FFFFFF;
  }
  .list {
    border-radius: 0;
    border: unset;
    li {
      font-family: 'Rubik';
      font-weight: 400;
      font-size: 16px;
      line-height: 105%;
      background: #323248;
      border: unset;
      color: #FFFFFF;
      padding: 10px;
    }
  }
}
.nice-select .option:hover,
.nice-select .option.focus,
.nice-select .option.selected.focus {
  background: #242440;
}
.nice-select.open:after {
  transform: rotate(180deg) ;
}
.form {
  &-group {
    input {
      background: #323248;
      border-radius: 7px;
      font-size: 14px;
      line-height: 120%;
      border: transparent;
      color: #ffffff;
      padding: 0 20px;
      &::placeholder {
        font-size: 14px;
        line-height: 120%;
        color: #7D7D97;
      }
    }
    span {
      display: block;
      font-size: 16px;
      line-height: 120%;
    }
  }
  &-radio {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    &:not(:last-child) {
      margin-right: 10px;
    }
    .radio {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      margin: 0;
      opacity: 0;
      cursor: pointer;
      &:checked~label {
        background: #6333B1;
      }
    }
    label {
      min-width: 108px;
      font-size: 14px;
      line-height: 120%;
      background: #323248;
      border-radius: 60px;
      padding: 10px 15px;
    }
  }
  &-check {
    position: relative;
    display: flex;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100% !important;
      height: 100% !important;
      margin: 0;
      opacity: 0;
      &:checked~label:before {
        opacity: 1;
        pointer-events: all;
      }
    }
    label {
      font-size: 16px;
      line-height: 140%;
      padding-left: 30px;
      a {
        display: block;
        color: #AE7CFF;
        position: relative;
        z-index: 3;
        line-height: 140%;
      }
      &:after {
        content: '';
        position: absolute;
        top: -2px;
        left: 0;
        width: 20px;
        height: 20px;
        border: 1px solid #7D7D97;
        border-radius: 5px;
      }
      &:before {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 20px;
        height: 18px;
        background-image: url("../img/check-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}
.select2 {
  width: 100% !important;
  &-selection {
    height: 50px;
    background: #323248 !important;
    display: flex !important;
    align-items: center;
    border-radius: 7px !important;
    &--multiple {
      border: 1px solid #323248 !important;
    }
    &:focus {
      border: 1px solid #323248;
    }
    textarea {
      color: #ffffff;
    }
  }
  &-dropdown {
    border: unset;
    background: transparent;
  }
 &-results__options {
   border-radius: 7px;
   max-height: 186px !important;
   &::-webkit-scrollbar {
     width: 3px;
   }
   &::-webkit-scrollbar-track {
     background: #ffffff;
     border-radius: 3px;
   }
   &::-webkit-scrollbar-thumb {
     background: #323248;
     border-radius: 3px;
   }
 }
  &-results__option {
    background: #333347;
    height: 42px;
    padding: 0 20px;
    display: flex;
    align-items: center;
  }
  &-results__option--selectable {
    background: #333347 !important;
  }
  &-results__option--selected {
    background-image: url("../img/check-icon3.svg") !important;
    background-repeat: no-repeat !important;
    background-position:top 50% right 20px !important;
    background-color: #49495F !important;
  }
}
.select2-container--default .select2-selection--multiple {
  padding: 0 20px;
  overflow-x: auto;
  &::-webkit-scrollbar {
    height: 3px;
  }
  &::-webkit-scrollbar-track {
    background: #ffffff;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #323248;
    border-radius: 3px;
  }
}
.select2-container .select2-selection--multiple .select2-selection__rendered {
  display: flex;
  align-items: center;
}
.select2-container--default .select2-selection--multiple .select2-selection__choice {
  min-width: 70px;
  height: 30px;
  background: #49495F;
  border-radius: 40px;
  color: #ffffff;
  border: 1px solid transparent;
  margin: 0;
  padding: 5px 25px 5px 10px;
  display: flex;
  align-items: center;
  position: relative;
  &:not(:first-child) {
    margin-left: 10px;
  }
  button {
    position: absolute;
    top: 50%;
    left: calc(100% - 24px);
    transform: translate(0, -50%);
    width: 14px;
    height: 14px;
    background-image: url("../img/select-close.svg");
    background-repeat: no-repeat;
    background-position: center;
    border-right: none;
    &:hover {
      background-color: transparent;
    }
    span {
     opacity: 0;
    }
  }
  span {
    font-size: 14px;
    line-height: 105%;
  }
}
.select2-search {
  width: 100%;
}
.select2-container--default .select2-search--inline .select2-search__field {
  margin: 0;
}
.modal {
  background: rgba(0, 0, 0, 0.4);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 700px;
    max-width: 100%;
    position: relative;
    background: #1F1F31;
    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
    border-radius: 15px;
    padding: 90px 50px;
    margin: 0 auto;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 330px;
      height: 330px;
      border-radius: 50%;
      background: radial-gradient(50% 50% at 50% 50%, rgba(117, 74, 239, 0.7) 0%, rgba(31, 31, 49, 0) 100%);
      pointer-events: none;
    }
    &2 {
      padding: 40px 30px;
      &:after {
        content: none;
      }
    }
    @media screen and (max-width: 992px) {
      margin: 50px auto 0;
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    top: 30px;
    right: 30px;
    width: 40px;
    height: 40px;
    background: #45456D;
    border-radius: 50%;
  }
  &__content {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__desc {
    max-width: 260px;
    margin: 0 0 0 auto;
    margin-left: 50px;
  }
  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 120%;
  }
  &__title4 {
    font-weight: 500;
  }
  &__text {
    font-size: 16px;
    line-height: 120%;
    color: #CBCBEA;
    margin-top: 25px;
  }
  &__img {
    width: 391px;
    position: absolute;
    z-index: 2;
    left: -40px;
    bottom: -40px;
    &3 {
      width: 260px;
    }
    img {
      width: 100%;
     height: auto;
    }
  }
  &__img2 {
    width: 315px;
  }
  &__form {
    margin-top: 40px;
    &-checkeds {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 30px;
      border-bottom: 1px solid #45456D;
      &2 {
        justify-content: start;
        padding-bottom: 60px;
      }
    }
    &-chequed {
      width: 50%;
      height: 200px;
      position: relative;
      &2 {
        max-width: 240px;
        height: 140px;
      }
      &:not(:first-child) {
        margin-left: 30px;
      }
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        opacity: 0;
        cursor: pointer;
        &:checked~label {
          border: 1px solid #AE7CFF;
        }
        &:hover {
          &~label {
            border: 1px solid #AE7CFF;
          }
        }
      }
      label {
        width: 100%;
        height: 100%;
        background: #1F1F31;
        box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
        border-radius: 15px;
        border: 1px solid transparent;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 15px;
        img {
          width: 100%;
          max-width: 115px;
          height: auto;
        }
      }
      .chequed-title {
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        text-align: center;
        margin-top: 17px;
        span {
          font-size: 14px;
        }
      }
    }
    &-desc {
      border-top: 1px solid #45456D;
      padding-top: 40px;
      text-align: center;
    }
    &-text {
      font-size: 22px;
      line-height: 140%;
      span {
        color: #AE7CFF;
      }
    }
    &-text2 {
      font-size: 14px;
      line-height: 140%;
      color: #9B9BB7;
      margin-top: 15px;
    }
    &-btn {
      width: 240px;
      height: 60px;
      margin: 45px auto 0;
    }
  }
  &__subtitle {
    font-size: 14px;
    line-height: 120%;
    color: #9B9BB7;
    margin-top: 10px;
  }
  .rating {
    display: flex;
    align-items: flex-end;
    font-size: 35px;
    line-height: 0.75;
    &__title {
      font-size: 18px;
      line-height: 120%;
    }
    &__body {
      position: relative;
      &:before {
        content: '★★★★★';
        display: block;
      }
    }
    &__active {
      position: absolute;
      width: 0%;
      height: 100%;
      top: 0;
      left: 0;
      overflow: hidden;
      &:before {
        content: '★★★★★';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        color: #FFC700;
      }
    }
    &__items {
      display: flex;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
    &__item {
      flex: 0 0 20%;
      height: 100%;
      margin: 0;
      opacity: 0;
      cursor: pointer;
    }
    &__value {
      font-size: 0%;
      line-height: 0;
    }
  }
  .form-rating {
    margin-top: 35px;
    &__group {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 63px;
      background: #161628;
      border-radius: 10px;
      padding: 0 20px;
      &:not(:first-child) {
        margin-top: 10px;
      }
      &:hover {
        background: #323248;
      }
      &2 {
        margin-top: 30px
      }
    }
    &__btn {
      width: 200px;
      height: 50px;
      margin: 0 auto;
    }
  }
  &-withdrawal {
    min-height: 138px;
    background: #323248;
    border-radius: 10px;
    padding: 15px 20px;
    margin-top: 40px;
    &__top {
      display: flex;
      align-items: center;
    }
    &__title {
      font-size: 16px;
      line-height: 120%;
      margin-right: 65px;
    }
    &__info {
      display: flex;
      align-items: center;
      font-size: 14px;
      line-height: 120%;
      color: #9696AE;
    }
    &__info span {
      margin-left: 5px;
    }
    &__group {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 12px;
      margin-bottom: 7px;
      input {
        width: 50%;
        height: 50px;
        background: #1F1F31;
      }
    }
    &__advantages {
      width: 50%;
      height: 50px;
      border: 1px solid #AE7CFF;
      border-radius: 7px;
      margin-left: 20px;
      font-size: 15px;
      line-height: 120%;
      padding: 7px 20px;
      span {
        display: inline;
        color: #AE7CFF;
      }
    }
  }
  &-rejection {
    background: #323248;
    border-radius: 15px;
    padding: 20px 15px;
    margin-top: 35px;
    &__desc {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 15px 30px;
    }
    &__title {
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
    }
    &__info {
      display: flex;
      align-items: start;
    }
    &__price {
      display: flex;
      align-items: center;
    }
    &__discount {
      display: inline-block;
      font-size: 14px;
      line-height: 120%;
      background: #9039FF;
      border-radius: 5px;
      padding: 4px 8px;
      margin-left: 4px;
    }
    &__btns {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 40px;
    }
    &__btn {
      width: 150px;
      height: 50px;
      background: transparent;
      border: 1px solid #9B9BB7;
      border-radius: 60px;
      font-weight: 500;
      font-size: 16px;
      line-height: 120%;
      color: #9B9BB7;
      &:hover {
        background: #6333B1;
        border: 1px solid #6333B1;
      }
    }
    &__btn2 {
      width: 150px;
      height: 50px;
      font-size: 16px;
      margin-left: 20px;
    }
  }
  &__price-text {
    font-size: 18px;
    line-height: 120%;
    margin-left: 4px;
    span {
      font-size: 14px;
      line-height: 120%;
      color: #9696AE;
    }
  }
  &-premium {
    margin-top: 50px;
    &__advantages {
      display: grid;
      grid-template-columns: 1fr;
      gap: 30px;
    }
    &__advantage {
      display: flex;
      align-items: center;
      span {
        font-size: 16px;
        line-height: 120%;
        margin-left: 20px;
      }
    }
    &__info {
      background: #323248;
      border-radius: 10px;
      padding: 20px 20px 25px;
      margin-top: 40px;
    }
    &__subtitle {
      font-size: 16px;
      line-height: 120%;
      margin-bottom: 22px;
    }
    .select__wrap {
      position: relative;
      &--active {
        .select__list {
          display: block;
        }
      }
    }
    .select__list-default {
      width: 100%;
      background: #1F1F31;
      border-radius: 7px;
      position: relative;
      cursor: pointer;
      li {
        display: flex;
        align-items: center;
        min-height: 50px;
        height: 100%;
        padding: 10px 50px 10px 20px;
      }
    }
    .select-arrow {
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
    }
    &__desc {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 100%;
    }
    &__tern {
      font-size: 16px;
      line-height: 120%;
    }
    &__price {
      font-size: 16px;
      line-height: 120%;
      margin-left: 10px;
    }
    .select__list {
      background: #49495F;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
      border-radius: 7px;
      position: absolute;
      top: 100%;
      left: 0;
      display: none;
      width: 100%;
      li {
        display: flex;
        align-items: center;
        min-height: 50px;
        height: 100%;
        padding: 10px 20px;
        cursor: pointer;
        &:hover {
          background: #60607A;
          &:first-child {
            border-radius: 7px 7px 0 0;
          }
          &:last-child {
            border-radius: 0 0 7px 7px;
          }
        }
      }
      .li--active {
        background: #60607A;
        &:first-child {
          border-radius: 7px 7px 0 0;
        }
        &:last-child {
          border-radius: 0 0 7px 7px;
        }
      }
    }
    &__text {
      font-size: 14px;
      line-height: 140%;
      color: #9B9BB7;
      margin-top: 15px;
    }
    &__btn {
      width: 240px;
      height: 60px;
      margin: 40px auto 0;
    }
  }
}

@media screen and (max-width: 992px) {
  .modal {
    &__dialog {
      &:after {
        left: 50px;
        width: 200px;
        height: 200px;
      }
    }
    &__content {
      justify-content: space-between;
    }
    &__img {
      width: 250px;
      position: static;
    }
  }
}

@media screen and (max-width: 768px) {
  .display3 {
    font-size: 20px;
  }
  .display2 {
    font-size: 24px;
  }
  .form {
    &-check {
      label {
        font-size: 14px;
      }
    }
    &-group {
      span {
        font-size: 14px;
      }
    }
  }
  .modal {
    &-withdrawal__group {
      display: block;
      input {
        width: 100%;
      }
    }
    &-withdrawal__advantages {
      width: 100%;
      margin: 15px 0 0 0;
      min-height: 50px;
      height: 100%;
      br {
        display: none;
      }
    }
    &-rejection__desc {
      grid-template-columns: 1fr;
    }
  }
}

