  .modal {
    &__dialog {
      padding: 100px 30px 30px;
      &2 {
        padding: 80px 30px;
      }
      &:after {
        display: none;
      }
    }
    &__content {
      flex-direction: column;
    }
    &__img {
      width: 300px;
      margin: 50px auto -100px;
    }
    &__desc {
      max-width: 100%;
      width: 100%;
      margin: 0;
      order: -1;
    }
    &__form-chequed {
      height: 150px;
      &:not(:first-child) {
        margin-left: 15px;
      }
      label {
        img {
          max-width: 80px;
        }
      }
    }
    .rating {
      font-size: 25px;
      &__title {
        font-size: 14px;
      }
    }
    .form-rating {
      &__btn {
        height: 40px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .modal {
    &__close2 {
      top: 15px;
      right: 15px;
    }
    &-premium {
      &__term {
        font-size: 14px;
      }
      &__price {
        font-size: 14px;
      }
    }
  }
}

