.seller-main {
  padding: 70px 0;
  background-image: url("../img/seller-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__logo {
    display: flex;
    align-items: center;
  }
  &__img {
    width: 150px;
    position: relative;
    img {
      width: 100%;
    }
  }
  &__edit {
    position: absolute;
    z-index: 2;
    right: 0;
    bottom: 0;
    max-width: 40px;
  }
  &__desc {
    margin-left: 30px;
  }
  &__name {
    font-family: 'Neue Einstellung';
    font-weight: 700;
    font-size: 36px;
    line-height: 105%;
  }
  &__info {
    display: inline-flex;
    align-items: center;
    background: #323248;
    border-radius: 5px;
    padding: 6px 8px;
    margin-top: 10px;
    span {
      font-size: 14px;
      line-height: 120%;
      margin-left: 5px;
    }
  }
}
.seller {
  padding: 0 0 160px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
  }
  &__left {
    width: 387px;
    margin-right: 28px;
  }
  &__right {
    width: calc(100% - 415px);
    display: flex;
    justify-content: space-between;
  }
  &__personality {
    background: #1F1F31;
    border-radius: 15px;
    padding: 30px 30px 40px;
    &-title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        font-weight: 500;
        font-size: 20px;
        line-height: 120%;
      }
      button {
        width: 30px;
        height: 30px;
        background: #323248;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        margin-left: 15px;
        padding: 0;
        &:hover {
          background: #4e4e5f;
        }
      }
    }
    &-items {
      display: grid;
      grid-template-columns: 1fr 1.5fr;
      gap: 30px 15px;
      margin-top: 30px;
      padding: 15px 15px 30px;
    }
    &-item {
      font-size: 13px;
      line-height: 120%;
      &:nth-child(odd) {
        color: #7D7D97;
      }
    }
  }
  &__income {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    min-height: 79px;
    background: #1F1F31;
    border-radius: 15px;
    padding: 0 30px;
    &-desc {
      display: flex;
      align-items: center;
    }
    &-title {
      font-weight: 700;
      font-size: 20px;
      line-height: 120%;
    }
    &-amount {
      display: flex;
      align-items: center;
      margin-left: 30px;
      img {
        margin-top: -6px;
      }
      span {
        font-family: 'Neue Einstellung';
        font-weight: 700;
        font-size: 24px;
        line-height: 120%;
        color: #CBB7FF;
        margin-left: 6px;
      }
    }
    &-btn {
      width: 150px;
      height: 40px;
      background: transparent;
      font-size: 16px;
    }
  }
  &__additionally {
    width: 100%;
    background: #1F1F31;
    border-radius: 15px;
    padding: 30px;
    margin-top: 30px;
    &-title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 30px;
      span {
        font-weight: 500;
        font-size: 20px;
        line-height: 120%;
      }
      button {
        width: 30px;
        height: 30px;
        background: #323248;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        margin-left: 15px;
        padding: 0;
        &:hover {
          background: #4e4e5f;
        }
      }
    }
    &-subtitle {
      font-size: 16px;
      line-height: 120%;
      margin-bottom: 15px;
    }
    &-desc {
      font-size: 14px;
      line-height: 140%;
      color: #9696AE;
    }
    &-socials {
      margin-top: 30px;
    }
    &-social {
      display: flex;
      align-items: center;
      &:not(:first-child) {
        margin-top: 15px;
      }
      span {
        font-size: 14px;
        line-height: 120%;
        color: #9696AE;
        margin-left: 20px;
      }
    }
  }
  &__services {
    width: 387px;
    background: #1F1F31;
    border-radius: 15px;
    padding: 30px 10px 40px 15px;
    margin-left: 28px;
    flex-shrink: 0;
    &-title {
      font-family: 'Neue Einstellung';
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
    }
    &-title2 {
      font-weight: 500;
      font-size: 20px;
      line-height: 120%;
    }
    &-items {
      margin-top: 30px;
      overflow-y: auto;
      height: 530px;
      padding-right: 10px;
      &::-webkit-scrollbar {
        width: 14px;
      }
      &::-webkit-scrollbar-track {
        background: #323248;
        border-radius: 15px;
      }
      &::-webkit-scrollbar-thumb {
        background: #7D7D97;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 15px;
      }
    }
    &-item {
      display: flex;
      position: relative;
      border-radius: 5px;
      padding: 15px 65px 15px 10px;
      &:hover {
        background: #323248;
        .seller__services-edit {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    &-edit {
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
      width: 40px;
      height: 40px;
      background: #1F1F31;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      padding: 0;
      opacity: 0;
      pointer-events: none;
    }
    &-img {
      width: 70px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-left: 20px;
    }
    &-price {
      display: flex;
      align-items: center;
      margin-top: 5px;
    }
    &-info {
      display: inline-block;
      background: #9039FF;
      border-radius: 5px;
      padding: 4px 8px;
      font-size: 14px;
      line-height: 120%;
      margin-top: 4px;
    }
  }
  &__price-desc {
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 120%;
    margin-left: 4px;
    span {
      font-size: 14px;
      color: #9696AE;
    }
  }
}

@media screen and (max-width: 1400px) {
  .seller {
    &__inner {
      flex-direction: column;
    }
    &__right {
      width: 100%;
      margin: 28px 0 0 0;
    }
  }
}

@media screen and (max-width: 1050px) {
  .seller {
    &__income {
      display: block;
      padding: 30px 20px;
      &-btn {
        margin-top: 15px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .seller-main {
    &__img {
      width: 100px;
    }
    &__name {
      font-size: 24px;
    }
    &__desc {
      margin-left: 15px;
    }
  }
  .seller {
    &__left {
      width: 100%;
      margin: 0;
    }
    &__right {
      order: -1;
      flex-direction: column;
      position: relative;
      padding-top: 150px;
      margin: 0;
      &-info {
        width: 100%;
        margin-top: 28px;
      }
    }
    &__services {
      order: -1;
      width: 100%;
      margin: 28px 0 0 0;
    }
    &__income {
      position: absolute;
      top: 0;
      left: 0;
      height: 150px;
      margin: 0;
    }
    &__additionally {
      margin: 0;
    }
    &__personality {
      margin-top: 28px;
    }
  }
}

@media screen and (max-width: 768px) {
  .seller-main {
    padding: 40px 0;
  }
  .seller {
    padding: 0 0 80px 0;
  }
}

@media screen and (max-width: 576px) {
  .seller-main {
    &__logo {
      max-width: 400px;
      margin: 0 auto;
    }
    &__img {
      width: 100%;
    }
    &__desc {
      flex-shrink: 0;
    }
  }
  .seller {
    &__services {
      &-item {
        &:hover {
          .seller__services-edit {
            background: #1F1F31;
          }
        }
      }
      &-edit {
        opacity: 1;
        background: #323248;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .seller {
    &__right {
      padding-top: 170px;
    }
    &__personality {
      padding: 30px 15px 40px;
      &-items {
        grid-template-columns: 1fr 1fr;
      }
    }
    &__income {
      height: 170px;
      padding: 30px 15px;
      &-desc {
        display: block;
      }
      &-amount {
        margin: 10px 0 0 0;
        span {
          font-size: 18px;
        }
      }
    }
    &__additionally {
      padding: 30px 15px;
    }
    &__services {
     &-items {
       &::-webkit-scrollbar {
         width: 7px;
       }
     }
      &-item {
        padding: 10px 35px 10px 10px;
      }
      &-img {
        width: 70px;
      }
      &-desc {
        margin-left: 10px;
      }
      &-info {
        font-size: 12px;
      }
      &-edit {
        right: 0;
        width: 30px;
        height: 30px;
      }
    }
    &__price-desc {
      font-size: 14px;
    }
  }
}