.settings {
  .container {
    max-width: 1820px;
    padding: 0;
  }
  &__inner {
    display: flex;
    padding-right: 15px;
  }
  &__sidebar {
    flex-shrink: 0;
    width: 402px;
    position: relative;
    background: #242440;
    &-close {
      position: absolute;
      top: 20px;
      right: 20px;
      cursor: pointer;
      display: none;
    }
    &-inner {
      width: 402px;
      height: 100%;
      padding: 40px 30px;
      position: fixed;
      background: #242440;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: 100%;
        width: 3000px;
        height: 5000px;
        background: #242440;
        pointer-events: none;
      }
    }
  }
  &__burger {
    position: relative;
    width: 24px;
    height: 16px;
    cursor: pointer;
    display: none;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #ffffff;
      transition: all 0.5s;
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2) {
        top: 50%;
        transform: translate(0, -50%);
      }
      &:nth-child(3) {
        bottom: 0;
      }
    }
  }
  &__tabs-title {
    height: 44px;
    font-size: 13px;
    line-height: 120%;
    color: #D9D9D9;
    display: flex;
    align-items: center;
    padding: 0 20px;
  }
  &__tab {
    height: 44px;
    font-size: 16px;
    line-height: 120%;
    display: flex;
    align-items: center;
    padding: 0 20px;
    border-radius: 7px;
    cursor: pointer;
    &--active {
      background: #101016;
    }
  }
  &__content {
    padding: 35px 0 130px 0;
    max-width: 630px;
    width: 100%;
    margin-left: 50px;
    display: none;
    &--active {
      display: block;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 120%;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 120%;
    margin-top: 30px;
  }
  &__avatar {
    display: flex;
    align-items: center;
    margin-top: 30px;
    &-img {
      flex-shrink: 0;
      width: 100px;
      img {
        width: 100%;
        height: auto;
      }
    }
    &-desc {
      margin-left: 20px;
      max-width: 310px;
    }
    &-text {
      font-size: 12px;
      line-height: 120%;
      color: #7D7D97;
    }
    &-btn {
      width: 120px;
      height: 34px;
      margin-top: 20px;
    }
  }
  &__form {
    margin-top: 50px;
    &-title {
      font-size: 16px;
      line-height: 120%;
      margin-bottom: 22px;
    }
    &-group {
      &:not(:first-child) {
        margin-top: 28px;
      }
      span {
        margin-bottom: 10px;
      }
      input {
        width: 100%;
        height: 50px;
      }
    }
    &-textarea {
      width: 100%;
      height: 149px;
      background: #323248;
      border-radius: 7px;
      font-size: 14px;
      line-height: 120%;
      color: #7D7D97;
      padding: 20px;
      border: none;
      resize: none;
    }
    &-btn {
      width: 150px;
      height: 40px;
      background: #21212D;
      border-radius: 50px;
      margin:  0 0 0 auto;
      font-size: 14px;
      line-height: 120%;
      color: #7D7D97;
      &:hover {
        background: #9039FF;
        color: #ffffff;
      }
    }
  }
  &__socials {
    margin-top: 40px;
    padding: 50px 0;
    border-top: 1px solid #323248;
    border-bottom: 1px solid #323248;
    &-title {
      font-size: 16px;
      line-height: 120%;
      margin-bottom: 15px;
    }
    &-group {
      &:not(:first-child) {
        margin-top: 28px;
      }
      span {
        font-size: 12px;
        color: #7D7D97;
        margin-bottom: 15px;
      }
      input {
        width: 100%;
        height: 50px;
      }
    }
  }
  &__info {
    margin-top: 40px;
    &-btn {
      font-size: 14px;
      line-height: 120%;
      color: #AE7CFF;
      margin-left: 15px;
      background: transparent;
      padding: 0;
      position: relative;
      i {
        width: 25px;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        pointer-events: none;
        img {
          width: 100%;
          height: auto;
        }
      }
      span:nth-child(2) {
        display: none;
      }
      &:hover {
        color: #ffffff;
      }
      &--active {
        i {
          display: none;
        }
        span:nth-child(1) {
          display: none;
        }
        span:nth-child(2) {
          display: block;
        }
      }
    }
  }
  &__country,
  &__gender,
  &__age {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-desc {
      display: flex;
      align-items: center;
    }
    &-title {
      font-size: 14px;
      line-height: 120%;
      color: #9696AE;
      min-width: 70px;
    }
    &-text {
      width: 140px;
      height: 30px;
      font-size: 14px;
      line-height: 120%;
      color: #ffffff;
      margin-left: 50px;
      background: transparent;
      border: none;
      padding: 5px;
      pointer-events: none;
      resize: none;
      overflow: hidden;
    }
  }
  .edit-text {
    &--active {
      background: #323248;
      border-radius: 7px;
      pointer-events: all;
    }
  }
  &__gender {
    margin-top: 17px;
  }
  &__age {
    margin-top: 17px;
  }
  &__info-account {
    margin-top: 25px;
    padding-bottom: 37px;
  }
  &__date,
  &__password,
  &__nick {
    display: flex;
    align-items: center;
    &-title {
      font-size: 14px;
      line-height: 120%;
      color: #9696AE;
      min-width: 130px;
    }
    &-text {
      font-size: 14px;
      line-height: 120%;
      margin-left: 50px;
    }
  }
  &__nick {
    margin-top: 37px;
  }
  &__password {
    justify-content: space-between;
    margin-top: 37px;
    &-desc {
      display: flex;
      align-items: center;
    }
    &-btn {
      font-size: 13px;
      line-height: 120%;
      color: #AE7CFF;
      background: transparent;
      padding: 0;
      margin-left: 15px;
    }
  }
  &__delete {
    display: flex;
    align-items: center;
    padding-top: 40px;
    border-top: 1px solid #323248;
    border-radius: 7px;
    &-btn {
      background: transparent;
      padding: 0;
      font-size: 14px;
      line-height: 120%;
      color: #9696AE;
    }
    &-btn2 {
      background: transparent;
      padding: 0;
      font-size: 14px;
      line-height: 120%;
      color: #AE7CFF;
      margin-left: 70px;
    }
  }
  &__financial {
    margin-top: 25px;
    &-info {
      font-size: 14px;
      line-height: 120%;
      color: #9696AE;
      margin-top: 37px;
      a {
       color: #AE7CFF;
      }
    }
  }
  &__card,
  &__yoomoney {
    display: flex;
    align-items: center;
    &-title {
      font-size: 14px;
      line-height: 120%;
      color: #9696AE;
      min-width: 130px;
    }
    &-text {
      font-size: 14px;
      line-height: 120%;
      margin-left: 50px;
    }
  }
  &__yoomoney {
    margin-top: 37px;
  }
  &__confirmation {
    margin-top: 100px;
    &-desc {
      font-size: 14px;
      line-height: 120%;
      color: #9696AE;
      margin-bottom: 38px;
    }
    &-form {
      margin-top: 37px;
    }
    &-card,
    &-yoomoney {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &-card {
      margin-bottom: 60px;
    }
    &-title {
      font-size: 14px;
      line-height: 120%;
      color: #9696AE;
    }
    &-input {
      margin: 0 15px;
     input {
       width: 300px;
       height: 50px
     }
    }
    &-file {
      position: relative;
      input {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        cursor: pointer;
      }
      label {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 160px;
        min-height: 50px;
        height: 100%;
        font-size: 14px;
        line-height: 120%;
        color: #CBB7FF;
        border: 1px solid #AE7CFF;
        border-radius: 60px;
        padding: 5px;
      }
    }
    &-btn {
      width: 150px;
      height: 40px;
      background: #21212D;
      border-radius: 50px;
      margin: 55px 0 0 auto;
      font-size: 14px;
      line-height: 120%;
      color: #7D7D97;
      &:hover {
        background: #9039FF;
        color: #ffffff;
      }
    }
  }
}


@media screen and (max-width: 1100px) {
  .settings {
    &__sidebar {
      width: 350px;
      &-inner {
        width: calc(350px - 30px);
      }
    }
    &__confirmation {
      &-card,
      &-yoomoney {
        display: block;
      }
      &-input {
        margin: 15px 0;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .settings {
    &__sidebar {
      width: 300px;
      &-inner {
       width: calc(300px - 15px);
        padding: 40px 0 0 15px;
      }
    }
    &__content {
      margin-left: 30px;
    }
    &__country,
    &__gender,
    &__age {
      &-text {
        margin-left: 30px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .settings {
    &__sidebar {
      position: fixed;
      z-index: 3;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4);
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
      &--active {
        opacity: 1;
        pointer-events: all;
      }
      &:after {
        display: none;
      }
      &-inner {
        position: relative;
        width: 300px;
        background: #242440;
        padding: 60px 15px;
        overflow-y: auto;
        &::-webkit-scrollbar {
          width: 3px;
        }
        &::-webkit-scrollbar-track {
          background: #ffffff;
          border-radius: 3px;
        }
        &::-webkit-scrollbar-thumb {
          background: #242440;
          border-radius: 3px;
        }
      }
      &-close {
        display: block;
      }
    }
    &__burger {
      display: block;
      margin: 30px 0 0 15px;
    }
    &__inner {
      display: block;
      padding-right: 0;
    }
    &__content {
      margin: 0 auto;
      padding: 30px 15px 100px;
    }
    &__avatar {
      &-img {
        width: 80px;
      }
    }
    &__date,
    &__nick,
    &__password {
      &-text {
        margin-left: 30px;
      }
    }
    &__delete {
      &-btn2 {
        margin-left: 30px;
      }
    }
    &__card,
    &__yoomoney {
      &-text {
        margin-left: 30px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .settings {
    &__sidebar-inner {
      width: 100%;
    }
    &__info {
      &-btn {
        span:nth-child(1) {
          display: none;
        }
        i {
          opacity: 1;
          position: static;
        }
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .settings {
    &__country,
    &__gender,
    &__age {
      &-text {
        font-size: 12px;
        margin-left: 15px;
      }
    }
    &__date,
    &__nick,
    &__password {
      &-text {
        margin-left: 15px;
      }
    }
    &__card,
    &__yoomoney {
      &-text {
        font-size: 12px;
        margin-left: 15px;
      }
    }
    &__confirmation-input {
      input {
        width: 280px;
        padding: 0 15px;
        font-size: 13px;
      }
    }
  }
}

@media screen and (max-width: 360px) {
  .settings {
    &__country,
    &__gender,
    &__age {
      display: block;
    }
    &__info-btn {
      margin: 15px 0 0 0;
    }
    &__password {
      display: block;
    }
  }
}