.premium-main {
  background: #5C33CF;
  &__inner {
    text-align: center;
    padding: 40px 0 110px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 400px;
      height: 400px;
      background: radial-gradient(50% 50% at 50% 50%, #FF4773 0%, rgba(92, 51, 207, 0) 100%);
    }
  }
  &__title {
    font-family: 'Earths Mightiest';
    font-weight: 700;
    font-size: 75px;
    line-height: 120%;
  }
  &__subtitle {
    font-weight: 500;
    font-size: 24px;
    line-height: 120%;
    margin-top: 22px;
  }
  &__img {
    position: absolute;
    z-index: 1;
    top: 15px;
    right: 0;
    width: 340px;
    img {
      width: 100%;
    }
  }
}
.premium {
  padding: 80px 0;
  &__inner {
    max-width: 820px;
    margin: 0 auto;
    text-align: center;
  }
  &__desc {
    margin-top: 45px;
    p {
      font-size: 20px;
      line-height: 130%;
      color: #D9D9D9;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &__btn {
    width: 292px;
    height: 60px;
    background: #5C33CF;
    border-radius: 60px;
    font-size: 18px;
    margin: 80px auto 0;
  }
}

@media screen and (max-width: 1400px) {
  .premium-main {
    &__inner {
      &:after {
        width: 300px;
        height: 300px;
      }
    }
    &__img {
      width: 270px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .premium-main {
    &__inner {
      &:after {
        display: none;
      }
    }
    &__img {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .premium-main {
    &__inner {
      padding: 20px 0 40px 0;
    }
    &__title {
      font-size: 48px;
    }
    &__subtitle {
      font-size: 20px;
      margin-top: 10px;
    }
  }
  .premium {
    padding: 40px 0;
    &__desc {
      margin-top: 30px;
      p {
        font-size: 16px;
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
    &__btn {
      font-size: 16px;
      margin-top: 40px;
    }
  }
}

@media screen and (max-width: 450px) {
  .premium {
    &__desc {
      p {
        font-size: 14px;
      }
    }
    &__btn {
      width: 200px;
      height: 40px;
      font-size: 14px;
    }
  }
}