.info-main {
  background: #5C33CF;
  &__inner {
    text-align: center;
    padding: 40px 0 160px;
    position: relative;
  }
  &__title {
    font-family: 'Earths Mightiest';
    font-weight: 700;
    font-size: 75px;
    line-height: 120%;
  }
  &__subtitle {
    font-weight: 500;
    font-size: 24px;
    line-height: 120%;
    margin-top: 22px;
  }
  &__img {
    position: absolute;
    z-index: 1;
    top: 15px;
    right: 250px;
    width: 233px;
    img {
      width: 100%;
    }
  }
}
.info {
  padding: 70px 0 190px;
  &__inner {
    max-width: 820px;
    margin: 0 auto;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
    margin-top: 30px;
  }
  &__item {
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 74px;
      background: #1F1F31;
      border-radius: 15px;
      padding: 10px 30px;
      cursor: pointer;
      &--active {
        .info__item-title {
          span {
            color: #B99DFF;
          }
        }
        .info__item-btn {
          transform: rotate(180deg);
        }
      }
    }
    &-title {
      display: flex;
      align-items: center;
      span {
        font-weight: 500;
        font-size: 16px;
        line-height: 120%;
        margin-left: 20px;
      }
    }
    &-btn {
      transition: all 0.5s;
      margin-left: 10px;
    }
    &-hidden {
      font-size: 16px;
      line-height: 140%;
      padding: 30px 30px 0;
      display: none;
    }
  }
}

@media screen and (max-width: 1400px) {
  .info-main {
    &__img {
      right: 50px;
      width: 200px;
    }
  }
}

@media screen and (max-width: 992px) {
  .info-main {
    &__img {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .info-main {
   &__inner {
     padding: 20px 0 40px 0;
   }
    &__title {
      font-size: 48px;
    }
    &__subtitle {
      font-size: 20px;
      margin-top: 10px;
    }
  }
  .info {
    padding: 40px 0;
    &__item {
      &-show {
        padding: 10px 15px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .info {
    &__item {
      &-title {
        span {
          font-size: 14px;
        }
      }
      &-hidden {
        font-size: 14px;
        padding: 15px 15px 0;
      }
    }
  }
}